import React, { Component } from 'react'
import { Chart } from '../../cmps/Chart/Chart';
import { bitcoinService } from '../../services/bitcoin.service';
import { userService } from '../../services/user.service';

export class StatsPage extends Component {
    state = {
        marketData: null,
        transactionsData: null,
    }

    async componentDidMount() {
        const user = await userService.getUser();
        if (!user) return this.props.history.push('/');
        var marketData = await bitcoinService.getMarketPrice();
        marketData = marketData.data.values.map(value => value.y);
        var transactionsData = await bitcoinService.getConfirmedTransactions();
        transactionsData = transactionsData.data.values.map(value => value.y);
        this.setState({ marketData, transactionsData })
    }

    render() {
        if (!this.state.marketData || !this.state.transactionsData) return <main className="flex-center"> <img src="https://flevix.com/wp-content/uploads/2020/01/Preloader.gif" height="200" alt=""/> </main>
        return (
            <main className="stats-page">
                <h2> Bitcoin Statistics </h2>
                <div className="chart-display">
                    <Chart data={ this.state.marketData } title="Market Price (USD)" subtitle="Average USD market price across major bitcoin exchanges." />
                    <Chart data={ this.state.transactionsData } title="Confirmed transactions per day" subtitle="The number of daily confirmed bitcoin transactoins" />
                </div>
            </main>
        )
    }
}
