import React, { Component } from 'react'
import './ContactPage.scss';
import { contactService } from '../../services/contact.service';
import { userService } from '../../services/user.service';
import { ContactList } from '../../cmps/ContactList/ContactList';
import { ContactFilter } from '../../cmps/ContactFilter/ContactFilter';
import _ from 'lodash';
import { Link } from 'react-router-dom';

export class ContactPage extends Component {
    state = {
        contacts: null,
        filterBy: null
    }
    async componentDidMount() {
        const user = await userService.getUser();
        if (!user) return this.props.history.push('/');
        this.loadContacts();
        this.onChangeFilter = _.debounce(this.onChangeFilter, 300)
    }

    async loadContacts() {
        const contacts = await contactService.getContacts(this.state.filterBy);
        this.setState({ contacts });
    }

    onChangeFilter = (filterBy) => {
        this.setState({ filterBy }, this.loadContacts)
    }

    onDeleteContact = async (e, id) => {
        e.preventDefault();
        await contactService.deleteContact(id);
        this.loadContacts();
    }

    render() {
        const { contacts } = this.state;
        if (!contacts) return <main className="flex-center"> <img src="https://flevix.com/wp-content/uploads/2020/01/Preloader.gif" height="200" alt=""/> </main>
        return (
            <main className="contact-page">
                <h2> Contacts </h2>
                <ContactFilter onChangeFilter={ this.onChangeFilter } />
                <ContactList contacts={ contacts } onDeleteContact={ this.onDeleteContact } />
                <Link to="/contact/edit" className="add-btn flex-center"> <i className="fas fa-plus"></i> </Link>
            </main>
        )
    }
}
