import React from 'react';
import './Chart.scss';
import { Sparklines, SparklinesLine, SparklinesBars } from 'react-sparklines';

export function Chart(props) {
    return (
        <div className="chart">
            <h3> { props.title } </h3>
            <Sparklines 
                data={props.data} 
                width={300} 
                height={50} 
                margin={5}>
                        <SparklinesBars style={{ stroke: "white", fill: "#ffc3f9", fillOpacity: ".25" }} />
                        <SparklinesLine style={{ stroke: "#ec4e6d", fill: "#fff" }} />
            </Sparklines>
            <p> { props.subtitle} </p>
        </div>
    )
}
