import React from 'react';
import './ContactPreview.scss';

export function ContactPreview(props) {
    const { _id, name, phone } = props.contact;
    return (
        <div className="contact-preview flex column align-center">
            <img src={`https://robohash.org/${_id}?set=set5`} alt="contact img" />
            <div className="info flex column space-between">
                <h3> { name } </h3>
                <p> { phone } </p>
            </div>
            <button onClick={ (e) => props.onDeleteContact(e, _id) }> 
                <i className="fas fa-trash"></i> 
            </button>
        </div>
    )
}
