import React from 'react'
import './MoveList.scss'
import Moment from 'react-moment';

export default function MoveList(props) {
    const movesToShow = (props.contact) ? props.moves.filter(move => move.toId === props.contact._id) : props.moves.slice(0,3) 
    if (!movesToShow.length) return <div className="move-list"> <h3 className="no-moves"> No transactions were made yet. </h3> </div>
    return (
        <div className="move-list">
            <h3 className="header"> Your { (props.contact) ? 'Moves' : 'Last 3 Moves' } </h3>
            {
                movesToShow.map( (move, idx) => {
                    return (
                        <div className="move" key={ idx }>
                            { !props.contact && <p> To: { move.to } </p> }  
                            <p> At: <Moment date={ move.at } format="D/MM/YYYY, HH:mm:ss A" /> </p>
                            <p> Amount: { move.amount } </p>
                        </div>
                    ) 
                })
            }
        </div>
    )
}
