import { storageService } from './storage.service';
const KEY = 'users';
const USER = 'user';

export const userService = {
    getUser,
    signup,
    login,
    logout,
    addMove
}

var users = storageService.load(KEY)
if (!users) {
    users = [
        { name: 'Eyal', coins: 100, moves: [] },
        { name: 'admin', coins: 100, moves: [] }
    ]
    storageService.store(KEY, users);
}

var loggedInUser = storageService.load(USER) ? storageService.load(USER) : null;

function getUser() {
    return Promise.resolve(loggedInUser);
}

function signup(name) {
    const user = {name , coins: 100, moves: []};
    users.push(user)
    storageService.store(KEY, users);
    loggedInUser = user
    storageService.store(USER, loggedInUser);
    return Promise.resolve(loggedInUser);
}

function login(name) {
    const user = users.find(user => user.name.toLowerCase() === name.toLowerCase());
    if (!user) return Promise.resolve();
    else {
        loggedInUser = user
        storageService.store(USER, loggedInUser);
        return Promise.resolve(loggedInUser);
    }
}

function logout() {
    loggedInUser = null;
    storageService.store(USER, null);
    return Promise.resolve()
}

function _update(user) {
    return new Promise((resolve, reject) => { 
        const index = users.findIndex( u => u._id === user._id)
        if (index !== -1) {
          users[index] = user
        }
        storageService.store(KEY, users);
        resolve(user)
      })
}

function addMove(contact, amount) {
    const move = {
        to: contact.name,
        toId: contact._id,
        amount,
        at: Date.now()
    }
    loggedInUser.coins -= amount;
    loggedInUser.moves.unshift(move);
    const updatedUser = _update(loggedInUser);
    storageService.store(USER, loggedInUser);
    return Promise.resolve(updatedUser);
}