import React, { Component } from 'react';
import './HomePage.scss';
import { Link } from 'react-router-dom';
import { userService } from '../../services/user.service';
import { bitcoinService } from '../../services/bitcoin.service';
import MoveList from '../../cmps/MoveList/MoveList';


export class HomePage extends Component {
    state = {
        user: null,
        bitRate: 0
    }
    async componentDidMount() {
        const user = await userService.getUser();
        if (!user) return this.props.history.push('/');
        const bitRate = await bitcoinService.getRate(1);
        this.setState({ user , bitRate: bitRate.data });
    }
    render() {
        if (!this.state.user || !this.state.bitRate) return <main className="flex-center"> <img src="https://flevix.com/wp-content/uploads/2020/01/Preloader.gif" height="200" alt=""/> </main>
        const { name, coins, moves } = this.state.user;
        return (
            <main className="home-page flex column align-center">
                <h1> Welcome, { name }! </h1>
                <Link to="/contact">
                    <button>
                        Get Started 
                    </button>
                </Link>
                <div className="user-details flex justify-center">
                    <p> <i className="fas fa-coins"></i> Your Coins: { coins } </p>
                    <p> <i className="fab fa-bitcoin"></i> BTC: { this.state.bitRate } </p>
                </div>
                <MoveList moves={ moves } />
            </main>
        )
    }
}
