import React, { Component } from 'react'
import './TransferFunds.scss'

export class TransferFunds extends Component {
    state = {
        amount: '',
        showError: false,
        errorMsg: ''
    }

    handleChange = ({ target }) => {
        this.setState({ amount: target.value, showError: false });
    }

    checkTransfer = (ev) => {
        const maxCoins = this.props.user.coins;
        const amount = this.state.amount;
        const msg = (amount > maxCoins) ? 'Insufficient funds, try a lower amount' : (!amount || amount < 0) ? 'Please insert a valid amount.' : '';
        if (msg) return this.setState({ showError: true, errorMsg: msg })
        this.props.onAddMove(ev, amount)
        this.setState({ amount: '' })
    }

    render() {
        const { amount } = this.state
        return (
            <div className="transfer-funds">
                <h3> Transfer funds to { this.props.contact.name } </h3>
                <p> Your funds: { this.props.user.coins } </p>
                <form onSubmit={ this.checkTransfer }>
                    <input type="number" value={amount} placeholder="Transfer amount..." onChange={ this.handleChange } />
                    {this.state.showError && <small> { this.state.errorMsg } </small> }
                    <button> Send </button>
                </form>
            </div>
        )
    }
}
